@import "../../styles/common";

.pop-body-container {
  position: relative;
  @media screen and (max-height: 600px) {
    overflow-y: auto;
  }
  .pop-nft-details {
    position: relative;
    height: 100vh;

    .pop-head-content {
      display: flex;
      padding: 1rem;
      justify-content: space-between;

      .pop-bid-title {
        font-size: 2rem;
        font-weight: 800;
      }
      .close-button-pop svg {
        height: 3rem;
      }
    }

    .pop-bid-progress {
      background: #e7e7e940;
      .progress-complete {
        background: #000;
        padding: 0.25rem;
        width: 25%;
        border-top-right-radius: 1rem;
        border-bottom-right-radius: 1rem;
      }

      &.error-progress {
        .progress-complete {
          background: #f21e00;
        }
      }

      &.loading {
        .progress-complete {
          background: #9c9c9b;
        }
      }
    }

    .error-float-container {
      position: absolute;
      top: 8rem;
      padding: 0 2rem;
      z-index: 10;
      width: 100%;

      .error-container {
        background: #f21e00;
        margin-bottom: 0.6rem;
        border-radius: 0.5rem;
        padding: 1rem 1rem;
        color: white;
        display: flex;
        align-items: center;
        position: relative;

        .btn-cls {
          position: absolute;
          right: 0.4rem;
          top: 0.4rem;
        }

        .error-text {
          flex: 1;
          font-weight: 600;
          font-size: 0.9rem;
        }

        .time-details {
          flex: 1;

          .time-title {
            font-weight: 600;
            font-size: 0.9rem;
          }
          .time-counter {
            .alert-counter-time {
              font-weight: 600;
              font-size: 1.5rem;
            }

            .alert-counter-interval {
              font-weight: 400;
              font-size: 0.8rem;
            }

            .alert-counter-gap {
              margin-right: 1rem;
            }
          }
        }

        .btn-error-button {
          font-weight: 600;
          background: #f21e00;
          font-size: 0.8rem;
          outline: none;
          border: 1px solid white;
          color: white;
          padding: 0.5rem 1rem;

          &:hover {
            background: white;
            color: #f21e00;
          }
        }

        .error-details {
          .error-title {
            font-weight: 600;
            font-size: 1rem;
          }

          .error-desc {
            font-weight: 600;
            font-size: 0.8rem;
          }
        }
      }
    }

    .pop-nft-media {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      margin-top: 2rem;

      img,
      video {
        max-height: 25vh;
        object-fit: scale-down;
        border-radius: 0.7rem;
        overflow: hidden;
        @media screen and (max-width: 425px) {
          max-height: none;
          width: 80%;
        }
      }

      audio {
        margin-top: 1rem;
        width: 75%;
      }

      &.preview {
        img,
        video {
          max-height: 25vh;
          @media screen and (max-width: 425px) {
            max-height: none;
            width: 80%;
          }
        }
      }
    }
    .pop-author-name {
      font-weight: 600;
      font-size: 1rem;
    }

    .pop-nft-title {
      font-weight: 600;
      font-size: 1.4rem;
      padding: 0 1rem;
    }

    .input-bid-container {
      padding: 0 3rem;
      .input-bid-text {
        font-size: 1rem;
        color: #000000;
        font-weight: 600;
        font-weight: normal;
        text-align: start;
      }

      .input-bid-wrap {
        border: 2px solid #222;
        border-radius: 0.6rem;
        display: flex;
        align-items: center;

        .bid-currency {
          color: #000000;
          font-weight: 600;
          font-size: 1.8rem !important;
          padding-left: 1.1rem;
        }

        .input-bid {
          width: 100%;
          font-weight: 600;
          font-size: 1.8rem !important;
          padding: 0.6rem 1.1rem 0.6rem 0.1rem;
          outline: none;
          border-radius: 0.6rem;
          border: none;
        }
      }

      .input-quantity-container {
        border: 2px solid #e6d44d;
        border-radius: 0.6rem;
        display: flex;
        align-items: center;

        .input-quantity {
          width: 100%;
          font-weight: 600;
          font-size: 1.8rem !important;
          padding: 0.6rem 1.1rem;
          outline: none;
          border-radius: 0.6rem;
          border: none;
        }

        .quantity-to-value {
          color: #d3d3d6;
          font-weight: 600;
          padding-right: 1.5rem;
          font-size: 1.3rem !important;
        }
      }

      .balance-details {
        margin-top: 0.5rem;
        font-size: 0.8rem;
        font-weight: 800;
        color: #000;
      }

      &.error-bid {
        .input-bid-text {
          color: #f21e00;
        }

        .input-bid-wrap {
          border: 2px solid #f21e00;
          .bid-currency {
            color: #f21e00;
          }
          .input-bid {
            color: #f21e00;
          }
        }
      }

      &.error-balance-float {
        .input-quantity-container {
          border: 2px solid #f21e00;

          .input-quantity {
            color: #f21e00;
          }
        }
      }

      &.error-balance {
        .balance-details {
          color: #f21e00;
        }

        .input-bid-wrap {
          border: 2px solid #f21e00;
          .bid-currency {
            color: #f21e00;
          }
          .input-bid {
            color: #f21e00;
          }
        }

        .input-quantity-container {
          border: 2px solid #f21e00;

          .input-quantity {
            color: #f21e00;
          }
        }
      }
    }

    .sucess-title {
      text-align: center;
      padding-top: 2rem;
      @media screen and (max-width: 425px) {
        padding-top: 1rem;
      }
      svg {
        height: 4rem;
        width: 4rem;
      }

      .message {
        font-weight: 600;
        font-size: 1.6rem;
        padding: 0 2rem;
      }
    }

    .success-summary-container {
      padding: 0 4rem;
      .success-summary {
        display: flex;
        justify-content: space-between;
        font-weight: 600;
        font-size: 1rem;
        padding: 0.5rem 0 0.5rem 0;
        border-bottom: 1px solid #000000;

        .bold {
          font-weight: 800;
          font-size: 1.1rem;
        }
      }
    }

    .pop-body-content {
      height: calc(100% - 18rem);
      overflow: hidden;
      overflow-y: auto;
      &.success {
        height: calc(100% - 15rem);
      }
    }

    .bottom-area {
      position: absolute;
      bottom: 0;
      width: 100%;
      @media screen and (max-height: 480px) {
        position: static;
      }

      .terms {
        font-weight: 600;
        font-size: 0.9rem;
        text-align: center;
        margin-bottom: 0.5rem;
        padding: 0 1rem;
      }

      .bottom-content-pop {
        display: flex;
        justify-content: space-around;
        align-items: center;
        padding: 1rem;

        box-shadow: 0px -4px 3px #e91e6336;

        .back-button {
          cursor: pointer;
          font-weight: 600;
          font-size: 1.3rem;
          margin-left: 2rem;
        }
        .place-bid-button {
          width: 100%;
          text-align: center;
          display: flex;
          align-items: center;
          justify-content: center;

          .place-bid-btn-pop {
            font-weight: 600;
            padding-top: 0.6rem;
            padding-bottom: 0.6rem;
            border: 2px solid #000000;
            // width: 48% !important;
            font-size: 1rem;
            margin: 0 1%;

            &:disabled {
              background-color: #ea337f85;
              color: #9c9c9b;
              border: none;

              &.process {
                background-color: #000000;
                color: #ffffff;
              }
            }

            &:hover {
              background-color: #ffffff;
              color: #000000;
            }
          }
        }
      }
    }

    .pop-signin {
      margin: 10rem 2rem 10rem 2rem;
      .pop-signin-title {
        font-size: 2rem;
        font-weight: 800;
      }
    }
  }
}
